body {
  font: 100% Helvetica, sans-serif;
  color: #222; }

nav ul {
  margin: 0;
  padding: 0;
  list-style: none; }

nav li {
  display: inline-block; }

nav a {
  display: block;
  padding: 6px 12px;
  text-decoration: none; }

.message, .success, .warning {
  border: 1xp solid #ccc;
  padding: 10px;
  color: #333; }

.success {
  border-color: red; }

.warning {
  border-color: yellow; }

.container {
  width: 100%;
  border-color: #5f5f5f; }

article[role="main"] {
  float: left;
  width: 62.5%; }

.prefix-box-1 {
  border-radius: 10px 5px;
  -ms-border-radius: 10px 5px;
  -o-border-radius: 10px 5px;
  -moz-border-radius: 10px 5px;
  -webkit-border-top-left-radius: 10px;
  -webkit-border-top-right-radius: 5px;
  -webkit-border-bottom-right-radius: 10px;
  -webkit-border-bottom-left-radius: 5px; }

.prefix-box-2 {
  border-radius: 12px 7px; }

.vendor-prefixed {
  -webkit-transition: ease;
  transition: ease;
  -webkit-transform: perspective();
  transform: perspective();
  -webkit-transition-duration: 12;
  transition-duration: 12;
  border-radius: 10px;
  -webkit-box-shadow: 10px;
  box-shadow: 10px;
  background-clip: inherit;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -webkit-font-feature-settings: "liga" off;
  font-feature-settings: "liga" off;
  -webkit-appearance: 1px bold;
  -moz-appearance: 1px bold;
  appearance: 1px bold; }

